            .h2 3
          p <b>Crecimiento:</b> se caracteriza por rpidos aumentos en las ventas. En esta fase aparecen los competidores. (Kerin,2018) Es una etapa de aceptacin rpida en el mercado y de incremento en las utilidades. (Kotler,2017)
        
        .tarjeta-numerada.color-secundario.px-5.py-4.mb-3.borde-y
