      h2 1.2. Servicios
    
    p.mb-3 Un servicio es una forma de producto que consiste en actividades, beneficios o satisfacciones que se ofrecen en venta y siendo esencialmente intangibles, no tienen como resultado la propiedad de algo. Algunos ejemplos son los servicios bancarios, el hospedaje en hoteles, los vuelos en aerolneas, el comercio minorista y la comunicacin inalmbrica (Kotler,2017).
 
    p Cmo se disea un concepto de servicio? Para Lovelock (2015) El mercadeo de servicios experimentados reconocen la necesidad de adoptar una perspectiva integral del desempeo que desean que sus clientes experimenten. La propuesta de valor debe considerar e integrar tres componentes:
    //- Aqu viene nueva imagen

    .row.justify-content-center.mb-5
        .col-lg-10
            .tarjeta-avatar-b.mb-5.align-items-center
