                .tarjeta-numerada__numero
                    .h2 1
                .h4 Marca corporativa
                p Se habla de marca corporativa cuando identifica a una empresa; puede, incluso, ser la misma de sus productos. Las empresas muy pequeas y con un portafolio poco diversificado o empresas de servicios, no asignan a cada producto una marca, sino que todo lo anuncian y comercializan con la marca de la organizacin, es decir, con la marca corporativa. Para el caso de empresas grandes y diversificadas, la marca corporativa regularmente es diferente a la de sus productos. Ej. Multinacionales extranjeras Unilever y Procter & Gamble y las multinacionales colombianas Nutresa y Quala. (Hoyos,2016

        .col-lg-6
            .tarjeta-numerada.color-primario.p-5.h-100
                .tarjeta-numerada__numero
                    .h2 2
                .h4 Marca de producto
                p La marca de producto es aquella que se usa para identificar un bien o un servicio en particular. Como ejemplo de estas marcas se pueden mencionar: Fruco, Coco, Axe, Lux, Rexona, Vasenol y Knorr, que pertenecen a Unilever, una de las empresas lderes en consumo masivo a nivel mundial. (Hoyos,2016)
    .row.mb-5    
        .col-lg-6
            .tarjeta-numerada.color-primario.p-5.h-100
                .tarjeta-numerada__numero
                    .h2 3
                .h4 Marcas propias o marca de distribuidor
                p Es aquella que fabrica el canal para vender en sus puntos de venta, compitiendo con las marcas de los fabricantes. Se clasifican en dos: marca blanca se venden con el nombre del detallista (marca xito o Ekono) y las privadas que se venden con un nombre diferente al nombre de la cadena, pero que son fabricadas por esta o fabricadas por un tercero para esta. Arkitect y Bronzini, en vestuario; Simply, en electrodomsticos y Taeq en alimentos, son ejemplos de marcas privadas de almacenes xito. (Hoyos,2016)
        .col-lg-6
            .tarjeta-numerada.color-primario.p-5.h-100
