                    .h2 4
                .h4 Marca colectiva
                p La desarrollan asociaciones de productores, fabricantes, prestadores de servicios, organizaciones o cualquier grupo de personas para identificar sus bienes y servicios. La marca colectiva busca explotar el buen nombre del cual gozan los integrantes y por extensin, la asociacin misma, generando una ventaja competitiva frente a otros fabricantes que trabajan de manera individual. Ejemplo la Federacin de Ganaderos Fedegan, present al mercado una marca colectiva para respaldar la produccin de carne de seis frigorficos asociados. El sello garantiza el origen del ganado, el sistema de clasificacin de canales, los mecanismos de refrigeracin y maduracin de las carnes (Hoyos,2016)

    .row.mb-5    
        .col-lg-6
            .tarjeta-numerada.color-primario.p-5.h-100
                .tarjeta-numerada__numero
                    .h2 5
                .h4 Denominacin de origen
                p Es el nombre o indicacin de un lugar geogrfico, que puede ser un pas, una regin determinada o una ciudad, que se asocia a un producto con el fin de darle ms valor frente a los consumidores, ya que garantiza que dicho producto sea originario de un lugar y, por las costumbres de produccin o transformacin de sus habitantes, tiene unas caractersticas y una reputacin que lo hacen diferente de los productos semejantes provenientes de otros lugares geogrcos (SIC, 2012) En Colombia se puede mencionar las artesanas de Rquira, el bizcocho de Achira de Huila y el emblemtico Caf de Colombia. (Hoyos,2016)
        .col-lg-6
            .tarjeta-numerada.color-primario.p-5.h-100
                .tarjeta-numerada__numero
                    .h2 6
                .h4 Marca de certificacin
                p Son las que se utilizan para garantizar una calidad o estndar de un producto o servicio (SIC, 2012). Estn las marcas ingrediente, los ecosellos, y las marcas que certifican que las empresas o productos cumplen con ciertas normas de calidad. Ejemplos Cotton USA. (Hoyos,2016)

    .row.mb-5    
        .col-lg-6
            .tarjeta-numerada.color-primario.p-5.h-100
                .tarjeta-numerada__numero
                    .h2 7
                .h4 Marcas destino
                p Son las desarrolladas para pases (marca pas), para ciudades o zonas de las ciudades. Hoy en da es comn hablar de city marketing, haciendo referencia al marketing que se hace para un destino turstico y de inversin. La marca pas se usa para hacer promocin hacia el exterior, buscando bsicamente la promocin de las exportaciones y del turismo, adems la promocin de la inversin extranjera. (Hoyos,2016)
        .col-lg-6
            .tarjeta-numerada.color-primario.p-5.h-100
                .tarjeta-numerada__numero
                    .h2 8
                .h4 Marcas personales
                p Aunque desde lo legal no existe este tipo de marca, el nombre de las personas, que existe en el concepto del registro civil y no del marcario, es una marca y, desde el marketing, debe recibir el tratamiento que se da a una marca comercial. Esto es importante para quienes viven del prestigio de sus nombres, como los profesionales independientes, los polticos y los artistas e, inclusive, quienes son empleados. Personal branding Ej. Natalia Paris, quien comercializa, entre otros productos, ropa interior, calzado, aceites, perfumes, revistas y servicios de telecomunicaciones. 

 
    h3.titulo-tercero   Construccin de marcas fuertes

    .bloque-texto-a.color-secundario.p-4.p-md-5.mb-5 
        .row.m-0.align-items-center.justify-content-between
            .col-lg-4.mb-4.mb-lg-0
                figure
                    img(src="@/assets/curso/img_20.png")
            .col-lg-8
                .bloque-texto-a__texto.p-5
                    p Kotler (2017) determina que el branding representa una decisin difcil y las principales decisiones de estrategia de marca implican la seleccin del nombre de marca, co-branding y el desarrollo de marca.


    .row.justify-content-center.mt-5.mb-5
        .col-lg-4
            .h3.titulo-tercero  Seleccin del nombre de marca.
            p.mb-5  Un buen nombre contribuye al xito de un producto; sin embargo, encontrar el mejor nombre de marca es una tarea difcil. Inicia con una revisin cuidadosa del producto y de sus beneficios, del mercado meta y de las estrategias de marketing propuestas. Despus de eso, la asignacin del nombre se vuelve una combinacin de ciencia, arte y un poco de instinto. Algunas cualidades deseables para un nombre de marca son:
        .col-lg-6
            ul.lista-ul.mb-5.mt-5
                li 
                    i.fas.fa-check
                    |   Sugerir algo acerca de los beneficios y las cualidades del producto. Ej. Pinterest.
                li 
                    i.fas.fa-check
                    |   Fcil de pronunciar, reconocer y recordar. Ej. Twitter
                    
                li 
                    i.fas.fa-check
                    |   Distintivo.
                li 
                    i.fas.fa-check
                    |   Se pueda ampliar. Ej. Amazon.com se inici como vendedor de libros en lnea, pero eligi un nombre que le permitiera expandirse a otras categoras.
                li 
                    i.fas.fa-check
                    |   Facilidad de traduccin y no causar confusin con los idiomas. 
                li 
                    i.fas.fa-check
                    |   Registrarse y protegerse legalmente.
    .row.justify-content-center
        .col-lg-10
            .h3.titulo-tercero  Co-branding
            p.mb-5  Si dos nombres de marca establecidos por diferentes compaas se usan en el mismo producto, surge el co-branding. Cada marca opera una categora diferente, las marcas combinadas crean mayor atractivo para el consumidor y aumentan el capital de marca. Ej. Taco Bell y Doritos formaron equipo para crear los Doritos Locos Tacos.                   

    .row.justify-content-center
        .col-lg-10
            .h3.titulo-tercero  Desarrollo de marca
            p.mb-5  Una compaa tiene cuatro opciones para desarrollar una marca: 
        .col-lg-10
            .row.mb-5
                .col-lg-6
                    .card.h-100
                        .card-body
                            .h5.card-title  Extensiones de lnea
                            p   Cuando una empresa utiliza el nombre de una marca para introducir al mercado nuevas formas, nuevos colores, tamaos, ingredientes o sabores de una categora de productos existente. 

                .col-lg-6
                    .card.h-100
                        .card-body
                            .h5.card-title  Extensiones de marca
                            p   Una extensin de marca implica el uso de un nombre de marca exitoso para lanzar productos nuevos o modificados en una categora nueva.  

            .row.mb-5
                .col-lg-6
                    .card.h-100
                        .card-body
                            .h5.card-title  Multimarcas
                            p   Las compaas a menudo introducen marcas adicionales en la misma categora de productos. 

                .col-lg-6
                    .card.h-100
                        .card-body
                            .h5.card-title  Marcas nuevas
                            p   Una empresa podra considerar que el poder de su nombre de marca existente est disminuyendo, y que necesita un nuevo nombre de marca. O quizs una empresa idee un nuevo nombre de marca cuando ingresa a una nueva categora de productos, donde ninguno de sus nombres de marca actuales es adecuado.


