                h2 1.5. Empaque
            .row.justify-content-center
                .col-lg-10
                    .bloque-texto-a.color-secundario.p-4.p-md-5.mb-5.bg-image-cover2 
                        .row.m-0.align-items-center.justify-content-between
                            .col-lg-4.mb-4.mb-lg-0
                                h2.mb-0 
                            .col-lg-8
                                .bloque-texto-a__texto.p-4.shadow-box
                                    p.mb-3 El empaque de un producto es cualquier contenedor en el que se ofrece para su venta y que incluye una etiqueta con informacin relevante para el consumidor (Kerin,2018).

                                    p El empaquetado de un producto tambin conocido como packaging tiene como objetivo fundamental atraer la atencin del cliente. La presentacin de un producto puede determinar que este sea un xito o un fracaso. En ocasiones ayuda a facilitar el reconocimiento de las marcas. El packaging contiene, protege, conserva, transporta, informa y vende (Martinez,2014).

            h3.titulo-tercero  Beneficios del empaque

            AcordionA.mb-5(tipo="b" clase-tarjeta="tarjeta color-secundario")
                .row(titulo="Beneficios de comunicacin").justify-content-center
                    .col-lg-10
                        p Uno de los principales beneficios del empaque es la informacin que se transmite al consumidor mediante la etiqueta, como las instrucciones de uso del producto y su composicin, las cuales son necesarias para satisfacer requisitos legales. Por ejemplo, el sistema de etiquetado de alimentos empacados y procesados proporciona un formato uniforme para informacin nutricional y diettica. (Kerin,2018) 

                .row(titulo="Beneficios funcionales").justify-content-center
                    .col-lg-10
                        p Los empaques suelen desempear una importante funcin, como de almacenamiento, comodidad, proteccin y calidad del producto. Los envases para alimentos son un ejemplo, y las empresas de bebidas los han hecho ms ligeros y fciles de apilar en anaqueles y refrigeradores. (Kerin,2018)

                .row(titulo="Beneficios percibidos").justify-content-center
                    .col-lg-10
                        p La percepcin que se crea en la mente de los consumidores. La forma del empaque, la etiqueta, el color y las imgenes distinguen a una marca de otra, comunican el posicionamiento de una marca y crean valor de marca, facilitando la formacin de asociaciones de marca fuertes, favorables y nicas. (Kerin,2018) 
                .row(titulo="Preocupacin por el medio ambiente").justify-content-center
                    .col-lg-10
                        p Debido a la inquietud generalizada en todo el mundo por el aumento de los desechos slidos y por el dficit de rellenos sanitarios adecuados, la cantidad, composicin y eliminacin de los materiales de empaquetado continan recibiendo mucha atencin con la finalidad de reducir los desechos slidos. (Kerin,2018)


