            #t_1_6.titulo-segundo.mt-5
                h2 1.6. Embalaje
            .row.justify-content-center
                .col-lg-12
                    .bloque-texto-a.p-4.p-md-5.mb-5.color-neutro 
                        .row.m-0.align-items-center.justify-content-between
                            .col-lg-8
                                .bloque-texto-a__texto.p-4.shadow-box
                                    p.mb-3 Hernndez (2017) Define el embalaje como un contenedor colectivo, que agrupa varios envases primarios y secundarios (empaques), con la finalidad de unificarlos, protegerlos, facilitar su manejo, almacenamiento, transporte y distribucin. Tambin se le conoce como el envase de distribucin. Las dos principales funciones del embalaje son unificar y proteger la mercanca durante su distribucin.

                            .col-lg-4.mb-4.mb-lg-0
                                figure
                                    img(src="@/assets/curso/img_34.png", alt="")


            .row.justify-content-center.align-items-center.mt-5
                .col-lg-12.mb-5
                    .card.r30.p-3.tarjeta--gris
                        .card-body
                            .h3 Funcin de unificacin
                            p.mb-3  Los productos de consumo no se pueden comercializar a granel en los sistemas de distribucin masiva. No es posible distribuir ningn producto si no est debidamente embalado. Las cargas formadas por productos embalados colectivamente llegan a ser homogneas o heterogneas. Cuando la carga es homognea no es difcil resolver su acomodo, pero cuando es heterognea (con pesos, dimensiones y materiales distintos), surge el problema de su acomodo, por la posibilidad de cadas y daos.
                .col-lg-12
                    .card.r30.p-3.tarjeta--gris.mb-5
                        .card-body
                            .h3 Funcin de proteccin
                            p.mb-3  La proteccin que da el embalaje al producto es de vital importancia para llevar a efecto su distribucin masiva. Los materiales del envase y el embalaje que se utilizan comnmente son:
                            .row.mb-5
                                .col-lg-4
                                    .tarjeta--boton.color-primario.p-4
                                        .row.justify-content-center.mb-3
                                            .col-7
                                                figure
                                                img(src='@/assets/curso/ic-px1.svg', alt='Texto que describa la imagen')
                                        h4.text-center Papel
                                        p.text-small Es uno de los materiales ms utilizados para el envase y puede combinarse con el aluminio y las pelculas plsticas. Su funcin principal es evitar fricciones entre las piezas, as como proporcionar una envoltura desechable que evite las raspaduras y la vibracin. El papel no resiste la humedad ni las grasas.
                                .col-lg-4
                                    .tarjeta--boton.color-primario.p-4
                                        .row.justify-content-center.mb-3
                                            .col-7
                                                figure
                                                img(src='@/assets/curso/ic-px2.svg', alt='Texto que describa la imagen')
                                        h4.text-center Cartn
                                        p.text-small Se utiliza ms como amortiguador que como envase. Como embalaje es posible darle propiedades impermeables aplicando pelculas de parafina o polietileno.

                                .col-lg-4
                                    .tarjeta--boton.color-primario.p-4
                                        .row.justify-content-center.mb-3
                                            .col-7
                                                figure
                                                img(src='@/assets/curso/ic-px3.svg', alt='Texto que describa la imagen')
                                        h4.text-center Madera
                                        p.text-small Se emplea para la fabricacin de tarimas, cajas, rejas o jaulas y carretes. Usualmente se utiliza la madera de pino por resistente y barata.
                            .row.mb-5
                                .col-lg-4
                                    .tarjeta--boton.color-primario.p-4
                                        .row.justify-content-center.mb-3
                                            .col-7
                                                figure
                                                img(src='@/assets/curso/ic-px4.svg', alt='Texto que describa la imagen')
                                        h4.text-center Plsticos
                                        p.text-small Sirve para fabricar envases y embalajes de alta productividad; sin embargo, el plstico no es biodegradable por lo que representa problemas ecolgicos graves a futuro. Este tipo de envases se deben reciclar.
                                .col-lg-4
