                                        .row.justify-content-center.mb-3
                                            .col-7
                                                figure
                                                img(src='@/assets/curso/ic-px5.svg', alt='Texto que describa la imagen')
                                        h4.text-center Vidrio
                                        p.text-small Es el material tradicional para el envase de alimentos, vinos, algunos frmacos y cosmticos. Algunas de sus caractersticas son su estabilidad qumica, que son 100% impermeables, la esterilizacin, refractabilidad, retornabilidad y la presentacin de calidad.

                                .col-lg-4
                                    .tarjeta--boton.color-primario.p-4
                                        .row.justify-content-center.mb-3
                                            .col-7
                                                figure
                                                img(src='@/assets/curso/ic-px6.svg', alt='Texto que describa la imagen')
                                        h4.text-center Metales
                                        p.text-small La hoja de lata es mayormente utilizada para la conservacin de alimentos, as como para el envasado de pinturas, solventes y productos qumicos, mientras que el aluminio lo es para los refrescos.

