                h2 1.7. Etiqueta

            img.mt-5.img-a(src="@/assets/curso/img_35.png").img-fluid 

            p.mt-5 Una etiqueta es una parte integral del empaque, la cual identifica el producto o marca, quin, dnde y cundo lo hizo, cmo debe usarse, cul es su contenido y sus ingredientes (Kerin,2018).

            p.mb-5 Hernndez (2017), explica la etiqueta como la parte del producto que contiene la informacin escrita sobre el artculo. Una etiqueta puede ser parte del embalaje o simplemente una hoja adherida directamente al producto. La etiqueta se utiliza para facilitar la identificacin de un producto al presentar la marca y un diseo grfico nico. A continuacin, describe los objetivos, elementos y caractersticas que deben tener las etiquetas. 
            
            h3.titulo-tercero   Objetivos de las etiquetas

