            h3.titulo-tercero.mt-5#tema1_7_1 Cdigos barras

            img.img-a(src="@/assets/curso/img_42.png").img-fluid 

            p.mt-5 Para la identificacin del producto se ha extendido el uso del cdigo de barras que se abrevia en ingls como UPC y significa Cdigo Universal del Producto. Es indispensable para aquellos bienes que se adquieren en el autoservicio o almacenes, a efecto de que las computadoras de una bodega o un comercio identifiquen plenamente cada artculo a travs de la lectura con rayo lser y se activen los programas de precios y facturacin, as como las entradas y salidas del inventario (Hernndez,2017).

            p En el sector del comercio, sobre todo, pero tambin en muchos otros sectores, el cdigo de barras responde a dos necesidades: la primera, tener una codificacin de cobertura universal, de modo que un tem tenga un cdigo que lo identifique en todo el mundo y que sea irrepetible; la segunda, que el cdigo sea ledo por una mquina, que se elimine la digitacin, para efectos de eliminacin de errores y de rapidez de operacin. El lenguaje de barras paralelas verticales, de diferente grosor y diferente espesor de separacin, con el que se crean cdigos alfanumricos, posibles de leer mediante un rayo lser y ser transmitidos a un sistema de computacin (Vlez,2014).

            p.mb-5 Actualmente, el Cdigo EAN 13. Es el ms utilizado entre todas las aplicaciones del GS1. A veces conocido como GTIN (Global Trade Item Number). Es el cdigo de uso corriente para lectura en las cajas registradoras en los autoservicios, es decir, es el sistema utilizado para la codificacin de bienes de consumo. El EAN 13 est compuesto por 13 dgitos, generalmente la estructura de la codificacin es la siguiente segn Vlez (2014):

            .row.justify-content-center.mb-5
                .col-lg-10
                    figure
                        img(src="@/assets/curso/img_43.png")

            .row.justify-content-center.align-items-center.mb-5
                .col-10.col-md-9
                    .row
                        .col-auto
                            img(src="@/assets/curso/img_44.png", alt="").img-icon
                        .col
                            p Cdigo QR (Quick Response), o cdigo 2D (Dos dimensiones). Creado en 1994 por una subsidiaria de Toyota. Almacena datos hasta de 7.000 caracteres. De comn aplicacin industrial en Japn y muy utilizado hoy en publicidad, luego de que a los telfonos mviles se les incluy software para leerlo. Es un cdigo libre de derechos de patente. (Vlez,2014)

            .titulo-tercero.mt-5#tema1_7_2 
                h3 Etiqueta RFID    

            img.img-a(src="@/assets/curso/img_45.png").img-fluid 

            p.mt-5 La tecnologa para identificar objetos mediante ondas de radiofrecuencia llamada Radio Frequency Identification (RFID), tambin denominada Dedicated Short Range Communication (DSRC). Esta tecnologa identifica el producto y sigue su ruta desde la fbrica hasta el consumidor. Tiene muchas ventajas: no es necesario que est en contacto directo con un escner ya que permite identificar y localizar al proveedor de artculos ms inmediato en la cadena de suministro de cualquier compaa, sector y ubicacin en el mundo; realiza lecturas ms rpidas y ms precisas (eliminando la necesidad de tener una lnea de visin directa); permite niveles ms bajos en el inventario; mejora el flujo de caja y la reduccin potencial de gastos generales; reduce mermas de inventario, capacidad de informar al personal o a los encargados del tiempo para reabastecer las estanteras o corregir la ubicacin del artculo que se ha colocado en el sitio equivocado; ayuda a conocer exactamente qu elementos han sido sustrados y de ser necesario, dnde localizarlos; previene el robo en tienda; lucha contra la falsificacin y facilita el retiro del mercado del producto que ha caducado (Hernandez,2017).

            p La etiqueta RFID se trata del cdigo EPC (Electronic Product Code) que se encuentra almacenado en un circuito integrado, llamado tag y que se transmite y se lee por radiofrecuencia en el sistema RFID. El EPC est administrado en el mundo por la organizacin EPC Global, filial de GS1. Su formato est conformado mediante 24 posiciones (alfanumricas), contra los 13 dgitos del cdigo GTIN del EAN-13, distribuidos en cuatro secciones (Vlez, 2014).

            p A continuacin, Vlez (2014) nos describe las posiciones de la etiqueta RFID.

            .row.justify-content-center.align-items-center.mt-5.mb-5
                .col-lg-3.mb-5
                    figure
                        img.h180(src="@/assets/curso/img_46.png", alt="")
                .col-lg-6
                    .row
                        .col-1.col-md-1.flecha
                            img(src="@/assets/curso/img_37.png").img-icon
                        .col-9.col-md-9
                            p #[strong Marca.]
                            p.mt-0 2 posiciones indican el nmero de versin utilizada en ese EPC
                    .row
                        .col-1.col-md-1.flecha
                            img(src="@/assets/curso/img_37.png").img-icon
                        .col-9.col-md-9
                            p #[strong Administrador EPC.]
                            p.mt-0 6 posiciones indican la empresa que se hace responsable.
                    .row
                        .col-1.col-md-1.flecha
                            img(src="@/assets/curso/img_37.png").img-icon
                        .col-9.col-md-9
                            p #[strong Categora de objeto.]
                            p.mt-0 6 posiciones identifican el tipo de producto.
                    .row
                        .col-1.col-md-1.flecha
                            img(src="@/assets/curso/img_37.png").img-icon
                        .col-9.col-md-9
                            p #[strong Nmero serial.]
                            p.mt-0 9 posiciones forman el identificador universal inequvoco de tem.

            p El sistema opera mediante una tarjeta o tag, que contiene un chip con una memoria interna, variable hasta millares de bytes, segn el modelo y una antena transmisora, adherida al tem, que puede ser objeto, animal o an, persona y que, mediante ondas de radio, entrega los datos de un cdigo a un lector RFID que los capta y puede transmitirlos a un sistema de computacin, para ser procesados. Indiscutiblemente el cdigo de barras ser sustituido rpidamente por aplicaciones y utilizaciones prcticas y rutinarias del sistema RFID (Vlez, 2014).

