                h2 1.8. Marketing Green

            img.img-a(src="@/assets/curso/img_47.png").img-fluid 

            p.mt-5 Kotler (2017), determina que tanto los consumidores como las empresas estn cambiando su perspectiva sobre los problemas ambientales y da las siguientes perspectivas:

            .row.justify-content-center.mt-5.my-5.mb-5
                    .col-10.col-md-9
                        ul.list-unstyled
                            li.justify-content-center.mt-5
                                i.fa.fa-angle-right.fa-lg.mr-3.text-color-orange
                                | Perspectiva de los consumidores: los consumidores han convertido sus legtimas preocupaciones ambientales en palabras y acciones, enfocndose en los productos verdes, la sustentabilidad corporativa y otros temas ambientales. 

                            li.justify-content-center.mt-5
                                i.fa.fa-angle-right.fa-lg.mr-3.text-color-orange
                                | Perspectiva de las empresas: generalmente, los productos verdes han destacado sus beneficios naturales, en este momento cobra importancia y popularidad los beneficios ambientales ya que los consumidores tambin se han vuelto escpticos acerca de discursos ecolgicos difciles de comprobar.
