 llamado tag y que se transmite y se lee por radiofrecuencia en el sistema RFID. El EPC est administrado en el mundo por la organizacin EPC Global, filial de GS1. Su formato est conformado mediante 24 posiciones (alfanumricas), contra los 13 dgitos del cdigo GTIN del EAN-13, distribuidos en cuatro secciones (Vlez, 2014).")]),_c('p',[_vm._v("A continuacin, Vlez (2014) nos describe las posiciones de la etiqueta RFID.")]),_vm._m(67),_c('p',[_vm._v("El sistema opera mediante una tarjeta o tag, que contiene un chip con una memoria interna, variable hasta millares de bytes, segn el modelo y una antena transmisora, adherida al tem, que puede ser objeto, animal o an, persona y que, mediante ondas de radio, entrega los datos de un cdigo a un lector RFID que los capta y puede transmitirlos a un sistema de computacin, para ser procesados. Indiscutiblemente el cdigo de barras ser sustituido rpidamente por aplicaciones y utilizaciones prcticas y rutinarias del sistema RFID (Vlez, 2014).")]),_c('Separador'),_c('br'),_c('br'),_vm._m(68),_c('img',{staticClass:"img-a img-fluid",attrs:{"src":require("@/assets/curso/img_47.png")}}),_c('p',{staticClass:"mt-5"},[_vm._v("Kotler (2017), determina que tanto los consumidores como las empresas estn cambiando su perspectiva sobre los problemas ambientales y da las siguientes perspectivas:")]),_vm._m(69),_c('Separador'),_c('br'),_c('br'),_vm._m(70),_c('img',{staticClass:"img-a img-fluid",attrs:{"src":require("@/assets/curso/img_48.png")}}),_c('p',{staticClass:"mt-5"},[_vm._v("El marketing social se aplica cuando existen organizaciones que, comprometidas con la situacin de una ciudad, desarrollan programas de comunicacin, buscando concientizar a los ciudadanos acerca de la necesidad de mantener en buen estado los parques, no fumar en lugares pblicos, respetar a los ancianos, navidad sin plvora o sin nios quemados, entre otras. En general el marketing social pretende generar, a travs de sus acciones, proteccin y bienestar para la comunidad (Pret